video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container{
    /* background: url('../../public/images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    justify-content: center;
    object-fit: contain;
}

.hero-container>h1{
    font-size: 100px;
    color: #fff;
    margin-top: -100px;
}

.hero-container>p{
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.hero-btns{
    margin-top: 32px;
}

.hero-btns .btn{
    margin: 6px;
}

.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 1023px) {
    .hero-container>h1{
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 740px) {
    .hero-container>h1{
        text-align: center;
        font-size: 50px;
        margin-top: -100px;
    }

    .hero-container>p{
        font-size: 22px;
    }

    .btn-mobile{
        display: block;
    }

    .btn{
        width: 100%;
    }
}